export default function(mock) {
  mock
    .onGet('/api/geographic/province')
    .reply(200, provinces)
    .onGet(/\/api\/geographic\/city\/(\w+)/)
    .reply(config => {
      const result = /\/api\/geographic\/city\/(\w+)/.exec(config.url)
      const id = result[1]
      return [200, cities[id] || []]
    })
}
